
import axios from 'axios'
import {raw, API_URL} from '../helper'
import {ref} from '@vue/runtime-core'

// private //
const productsMap = ref(new Map());

let products = productsMap.value

// public //
const main = {};
const PRODUCT_URL = API_URL + '/product'
// GET
main.getAllProducts = () => products

main.getAllProductsList = async () => {
    const res = await axios.get(PRODUCT_URL)
    return res.data;
}

main.getProduct = async (name) => {
    const res = await axios.get(PRODUCT_URL,{params: {name}} )
    return res
}

// SET
main.addProduct = (_product) => {
    const MSG_SUCCESS = "User added"
    const MSG_ERROR = "Error adding user"
    const MSG_INVALID = "Invalid user"

    return new Promise((resolve,reject) => {
        if(_product){
            const product = raw(_product)
       
            if(product.name && product.price){

                products.set(_product.name, product)
    
                axios.post(PRODUCT_URL ,product)
                .then(res => {
                    if(res.data === 222){
                        resolve(MSG_SUCCESS)
                    }
                })
                .catch(e => reject(MSG_ERROR))
                return
            }
            reject(MSG_INVALID)
        }
        reject(MSG_INVALID)
    })
   
}

main.deleteProduct = (name) => {
    return axios.delete(PRODUCT_URL ,{data:{name:name}})
}

main.updateProduct = (filter,product) => {
    return axios.put(PRODUCT_URL ,{filter:filter,product:product})
}

main.deleteAll = () => {
    return axios.delete(PRODUCT_URL ,{data:{}})
}
// Export
export default main