
import axios from 'axios'
import {raw, API_URL} from '../helper'
import {ref} from '@vue/runtime-core'

// private //
const categoriesMap = ref(new Map());

let categories = categoriesMap.value

// public //
const main = {};
const CATEGORY_URL = API_URL + '/category'
// GET
main.getAllCategories = () => categories

main.getAllCategoriesList = async () => {
    const res = await axios.get(CATEGORY_URL)
    return res.data;
}

main.getCategory = async (name) => {
    const res = await axios.get(CATEGORY_URL,{params: {name}} )
    return res
}

// SET
main.addCategory = (_category) => {
    const MSG_SUCCESS = "User added"
    const MSG_ERROR = "Error adding user"
    const MSG_INVALID = "Invalid user"

    return new Promise((resolve,reject) => {
        if(_category){
            const category = raw(_category)
       
            if(category.name){

                categories.set(_category.name, category)
    
                axios.post(CATEGORY_URL ,category)
                .then(res => {
                    if(res.data === 222){
                        resolve(MSG_SUCCESS)
                    }
                })
                .catch(e => reject(MSG_ERROR))
                return
            }
            reject(MSG_INVALID)
        }
        reject(MSG_INVALID)
    })
   
}

main.deleteCategory = (name) => {
    return axios.delete(CATEGORY_URL ,{data:{name:name}})
}

main.updateCategory = (filter,category) => {
    return axios.put(CATEGORY_URL ,{filter:filter,category:category})
}

main.deleteAll = () => {
    return axios.delete(CATEGORY_URL ,{data:{}})
}
// Export
export default main