import { createApp } from 'vue'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import ProductAPI from './data/product/ProductAPI'
import CategoryAPI from './data/category/CategoryAPI'
import Notification from '@kyvg/vue3-notification'
import PrimeVue from 'primevue/config';
import Dialog from 'primevue/dialog'

import GLOBAL_PRODUCT from './data/product/product'
import GLOBAL_CATEGORY from './data/category/category'

const app = createApp(App)

// JQUERY as global variable
window.$ = window.jQuery = require('jquery')

app.provide('ProductAPI',ProductAPI)
app.provide('CategoryAPI',CategoryAPI)
app.provide('GLOBAL_PRODUCT',GLOBAL_PRODUCT)
app.provide('GLOBAL_CATEGORY',GLOBAL_CATEGORY)

app.use(router)
app.use(PrimeVue)
app.use(Notification)

app.component('Dialog', Dialog);

app.mount('#app')


